.mato-button {
	@extend %button;
	min-height: $button-height;
	height: 3.2rem;
	line-height: 2rem;
	border: $border;
	border-radius: $border-radius;
	padding: 0.4rem 2rem;
	transition: all 0.3s ease-in-out;
	box-sizing: border-box;
	cursor: pointer;

	&:hover {
		color: white;
		background-color: $mato-green;
		border: $border-mato;
		text-decoration: none;
	}
}

.mato-button-alt {
	@extend %button;
	color: $mato-green;
	padding: 0.4rem 0;
	transition: all 0.3s ease-in-out;

	&:hover {
		color: $mato-green;
		text-decoration: underline;
	}
}